footer {
	background: var(--color-primary);
	padding: 0 0 3rem 0;
	text-align: center;
	font-size: 0.9rem;
	margin-top: 7rem;
}

footer a {
	color: var(--color-bg);
}

footer::before {
	display: flex;
	content: "";
	height: 0.2rem;
	background: var(--color-light);
}

.footer__logo {
	margin-top: 2rem;
	font-size: 2rem;
	font-weight: 500;
	margin-bottom: 2rem;
	display: inline-block;
}

.footer__logo img {
	width: 4rem;
	border-radius: 50%;
}

.footer__logo img:hover {
	transform: rotate(20deg);
	transition: var(--transition);
}

.permalinks {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 2rem;
	margin: 0 auto 3rem;
}

.footer__socials {
	display: flex;
	justify-content: center;
	gap: 1rem;
	margin-bottom: 4rem;
}

.footer__socials a {
	background: var(--color-bg);
	color: var(--color-white);
	padding: 0.8rem;
	display: flex;
	border: 1px solid transparent;
	border-radius: 0.8rem;
}

.footer__socials a:hover {
	background: transparent;
	color: var(--color-bg);
	border-color: var(--color-bg);
}

.footer__copyright {
	margin-bottom: 4rem;
	color: var(--color-bg);
}

/* ========== MEDIA QUERIES - SMALL DEVICES ========== */
@media screen and (max-width: 600px) {
	.permalinks {
		flex-direction: column;
		gap: 1.4rem;
	}

	.footer__socials {
		margin-bottom: 2rem;
	}
}
